















































import Vue from 'vue'
import { register } from '@/api/auth'

export default Vue.extend({
  name: 'Login',
  data () {
    return {
      user: {
        username: '',
        email: '',
        password: ''
      }
    }
  },
  methods: {
    async onSubmit () {
      try {
        // 获取表单数据
        // 表单验证
        // 提交表单
        const data = await register({
          user: this.user
        })
        console.log(data.user.username)
        // 根据返回结果处理后续操作
        this.$router.push({
          name: 'home'
        })
      } catch (err) {
        console.log('提交失败...', err)
      }
    }
  }
})
